import React, { Component } from "react";
import Navbar from "./Navbar";
import TextQuestion from "./TextQuestion";
import "react-step-progress-bar/styles.css";
import { ProgressBar } from "react-step-progress-bar";

class Page2 extends Component {
	submit(e) {
		e.preventDefault();
		const q3 = document.getElementById("Q3").value;
		const q4 = document.getElementById("Q4").value;
		const q5 = document.getElementById("Q5").value;
		const q6 = document.getElementById("Q6").value;
		localStorage.setItem("Q3", q3);
		localStorage.setItem("Q4", q4);
		localStorage.setItem("Q5", q5);
		localStorage.setItem("Q6", q6);
		window.location.href = "https://elephant-coverage.herokuapp.com/form/3";
	}

	componentWillMount() {
		if (!localStorage.getItem("Q1") || !localStorage.getItem("Q2")) {
			window.location.href = "https://elephant-coverage.herokuapp.com/form/1";
		}
	}

	existingValue(qid) {
		if (localStorage.getItem(qid)) {
			return localStorage.getItem(qid);
		} else {
			return "";
		}
	}

	back() {
		window.location.href = "https://elephant-coverage.herokuapp.com/form/1";
	}

	render() {
		return (
			<div className="container">
				<Navbar />
				<div className="page">
					<form onSubmit={this.submit}>
						<div className="questions">
							<TextQuestion
								question="Where do you park your cars overnight?"
								sub_question="Insurance rates vary depending on your ZIP code."
								qid="Q3"
								label="Address"
								val={this.existingValue("Q3")}
							/>
							<TextQuestion
								question="Tell us a little about yourself"
								qid="Q4"
								label="First Name"
								val={this.existingValue("Q4")}
							/>
							<TextQuestion
								qid="Q5"
								label="Last Name"
								val={this.existingValue("Q5")}
							/>
							<TextQuestion
								qid="Q6"
								label="Date of Birth (MM/DD/YYYY)"
								val={this.existingValue("Q6")}
							/>
						</div>
						<div className="next-page" onClick={this.back}>
							&larr; Go Back
						</div>
						<input type="submit" value="Next &rarr;" className="submit" />
					</form>
					<ProgressBar
						percent={40}
						filledBackground="linear-gradient(to right, #56ccf2, #2f80ed)"
					/>
				</div>
			</div>
		);
	}
}
export default Page2;
