import React, { Component } from "react";
import Navbar from "./Navbar";
import "react-step-progress-bar/styles.css";
import { ProgressBar } from "react-step-progress-bar";

class Page1 extends Component {
	componentWillMount() {
		if (
			!localStorage.getItem("zip_code") ||
			!localStorage.getItem("insurance_type")
		) {
			window.location.href = "https://elephant-coverage.herokuapp.com/";
		}
	}

	back() {
		window.location.href = "https://elephant-coverage.herokuapp.com/";
	}

	render() {
		return (
			<div className="container">
				<Navbar />
				<div className="page">
					<h1 className="success" style={{ textAlign: "left" }}>
						Done!
					</h1>
					<p className="success-msg">
						Thank you for your information. Our insurance agency will contact
						you within the next hour to offer the cheapest auto insurance plans
						available in the state of Michigan.
					</p>
					<div style={{ paddingTop: "40vh" }}></div>
					<div className="next-page" onClick={this.back}>
						&larr; Back to Home
					</div>
					<ProgressBar
						percent={100}
						filledBackground="linear-gradient(to right, #56ccf2, #2f80ed)"
					/>
				</div>
			</div>
		);
	}
}
export default Page1;
